<script>
import AppRoad from "@/components/Road.vue";
export default {
  name: "AppMain",
  components: {
    AppRoad,
  },
  data() {
    return {
      show: false,
      position1: true,
      position2: false,
      position3: false,
    };
  },
  methods: {
    changeShow() {
      this.show = !this.show;
    },
    changePosition(step) {
      this.position1 = false;
      this.position2 = false;
      this.position3 = false;
      if (step == 1) {
        this.position1 = true;
      }
      if (step == 2) {
        this.position2 = true;
      }
      if (step == 3) {
        this.position3 = true;
      }
    },
  },
};
</script>
<style>
.ex-va__container {
  background-color: #000;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.nav-list {
  list-style: none;
  display: flex;
  align-items: center;
}

.ex-va__wrapper {
  height: 997px;
  width: 935px;
  overflow: hidden;
  position: relative;
}

@media screen (max-width: 576px) {
  .ex-va__wrapper {
    height: 70vh;
    width: 100vw;
    overflow: hidden;
    position: relative;
  }
}

button {
  color: white;
  font-weight: 700;
  font-size: 20px;
}

.ex-va__action {
  background-repeat: no-repeat;
  background-size: 1580px 1000px;
  background-position: -320px 0px;
  height: 997px;
  width: 935px;
  margin: 0;
  overflow: hidden;
  position: absolute;
  top: 0;
  left: 0;
  transition: all ease-in-out 0.3s;
}

.ex-va__road {
  position: absolute;
  top: 0;
  left: 0;
}

.position-start {
  top: 0;
  left: 0;
}

.position-2 {
  top: -300px;
  left: -300px;
}

.position-3 {
  top: -600px;
  left: -600px;
}

svg {
  height: intrinsic;
}
.additionals {
  opacity: 0.4;
  -webkit-transition: 0.2s ease-in-out;
  -o-transition: 0.2s ease-in-out;
  transition: 0.2s ease-in-out;
}
#e_action {
  cursor: pointer;
  width: 100vw;
}
#e_action:hover .additionals {
  opacity: 1;
}
.waves {
  -webkit-transform-origin: center;
  -ms-transform-origin: center;
  transform-origin: center;
  -webkit-transform: scale(0.3) rotate3d(1, 0, 0, 56deg);
  transform: scale(0.3) rotate3d(1, 0, 0, 56deg);
  opacity: 0;
  -webkit-transition: 1s ease-in-out;
  -o-transition: 1s ease-in-out;
  transition: 1s ease-in-out;
}
.active .additionals {
  opacity: 1;
}
.active .waves {
  opacity: 1;
  -webkit-transform: scale(1) rotate3d(1, 0, 0, 56deg);
  transform: scale(1) rotate3d(1, 0, 0, 56deg);
}

.circle {
  will-change: transform, opacity;
  fill: transparent;
  fill-opacity: 0;
  stroke-width: 3px;
  -webkit-transform-origin: 50% 50%;
  -ms-transform-origin: 50% 50%;
  transform-origin: 50% 50%;

  -webkit-animation-duration: 5s;
  animation-duration: 5s;
  -webkit-animation-name: pulse;
  animation-name: pulse;
  -webkit-animation-iteration-count: infinite;
  animation-iteration-count: infinite;
}

.circle1 {
  stroke: url(#paint1);
  /* -webkit-filter: url(#filter1);
  filter: url(#filter1); */
  opacity: 0;
}
.circle2 {
  stroke: url(#paint2);
  -webkit-animation-delay: 1s;
  animation-delay: 1s;
  /* -webkit-filter: url(#filter2);
  filter: url(#filter2); */
  opacity: 0;
}
.circle3 {
  stroke: url(#paint3);
  -webkit-animation-delay: 2s;
  animation-delay: 2s;
  /* -webkit-filter: url(#filter3);
  filter: url(#filter3); */
  opacity: 0;
}
.circle4 {
  stroke: url(#paint4);
  -webkit-animation-delay: 3s;
  animation-delay: 3s;
  /* -webkit-filter: url(#filter4);
  filter: url(#filter4); */
  opacity: 0;
}
.circle5 {
  stroke: url(#paint5);
  -webkit-animation-delay: 4s;
  animation-delay: 4s;
  /* -webkit-filter: url(#filter5);
  filter: url(#filter5); */
  opacity: 0;
}
.levitation {
  -webkit-transition: all 5s ease-in-out;
  -o-transition: all 5s ease-in-out;
  transition: all 5s ease-in-out;
  -webkit-animation-duration: 5s;
  animation-duration: 5s;
  -webkit-animation-name: float;
  animation-name: float;
  -webkit-animation-iteration-count: infinite;
  animation-iteration-count: infinite;
  -webkit-animation-play-state: paused;
  animation-play-state: paused;
}
.levitation1 {
  -webkit-animation-name: float1;
  animation-name: float1;
}
.levitation2 {
  -webkit-animation-name: float2;
  animation-name: float2;
}

.active .levitation1,
.active .levitation2,
.active .levitation {
  -webkit-animation-play-state: running;
  animation-play-state: running;
}

@-webkit-keyframes pulse {
  0% {
    stroke-width: 3px;
    opacity: 1;
    -webkit-transform: scale(0.3);
    transform: scale(0.3);
    -webkit-filter: blur(0);
    filter: blur(0);
  }
  30% {
    -webkit-filter: blur(0);
    filter: blur(0);
  }
  70% {
    opacity: 0.6;
  }
  100% {
